            </symbol>
          </defs>
        </svg>
        <div class="container">
          <div class="checkout-order">
            <div class="page-title-normal">
              <h2 class="page-title-h2"><span>check out</span></h2>
            </div>
            <!-- process step -->
            <div class="check-step">
              <ul>
                <li class="cur"><span>Confirm</span> address</li>
                <li class="cur"><span>View your</span> order</li>
                <li><span>Make</span> payment</li>
                <li><span>Order</span> confirmation</li>
              </ul>
            </div>

            <!-- order list -->
            <div class="page-title-normal checkout-title">
              <h2><span>Order content</span></h2>
            </div>
            <div class="item-list-wrap confirm-item-list-wrap">
              <div class="cart-item order-item">
                <div class="cart-item-head">
                  <ul>
                    <li>Order contents</li>
                    <li>Price</li>
                    <li>Quantity</li>
                    <li>Subtotal</li>
                  </ul>
                </div>
                <ul class="cart-item-list">
                  <li v-for="item in cartList" v-show="item.checked == 1">
                    <div class="cart-tab-1">
                      <div class="cart-item-pic">
                        <img :src="'/static/'+item.productImage" :alt="item.productName">
                      </div>
                      <div class="cart-item-title">
                        <div class="item-name">{{item.productName}}</div>
                      </div>
                    </div>
                    <div class="cart-tab-2">
                      <div class="item-price">{{item.salePrice | currency('￥')}}</div>
                    </div>
                    <div class="cart-tab-3">
                      <div class="item-quantity">
                        <div class="select-self">
                          <div class="select-self-area">
                            <span class="select-ipt">×{{item.productNum}}</span>
                          </div>
                        </div>
                        <div class="item-stock item-stock-no">In Stock</div>
                      </div>
                    </div>
                    <div class="cart-tab-4">
                      <div class="item-price-total">{{(item.salePrice*item.productNum)| currency('￥')}}</div>
                    </div>
                  </li>
                </ul>
              </div>
            </div>

            <!-- Price count -->
            <div class="price-count-wrap">
              <div class="price-count">
                <ul>
                  <li>
                    <span>Item subtotal:</span>
                    <span>{{subTotal | currency('￥')}}</span>
                  </li>
                  <li>
                    <span>Shipping:</span>
                    <span>{{shipping | currency('￥')}}</span>
                  </li>
                  <li>
                    <span>Discount:</span>
                    <span>{{discount | currency('￥')}}</span>
                  </li>
                  <li>
                    <span>Tax:</span>
                    <span>{{tax | currency('￥')}}</span>
                  </li>
                  <li class="order-total-price">
                    <span>Order total:</span>
                    <span>{{totalPrice | currency('￥')}}</span>
                  </li>
                </ul>
              </div>
            </div>

            <div class="order-foot-wrap">
              <div class="prev-btn-wrap">
                <router-link class="btn btn--m" to="/address">Previous</router-link>
              </div>
              <div class="next-btn-wrap">
                <button class="btn btn--m btn--red" @click="payment">Proceed to payment</button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <n-footer></n-footer>
    </div>
</template>
<style>
</style>
<script>
  import './../src/assets/css/checkout.css'
  import NavHeader from './../src/components/Header.vue'
  import NavBread from '@/components/Bread.vue'
  import Footer from './../src/components/Footer.vue'
  import Modal from './../src/components/Modal.vue'
  import axios from 'axios'
  import {currency} from './../utils/currency'

    export default {
      data() {
        return {
          cartList:[],//购物车数组
          shipping:200,//
          discount:30,
          tax:20,
        }
      },
      filters: {
        currency: currency,
      },
      computed: {
        subTotal () {
          let total = 0;
          this.cartList.forEach(function (item) {
            if(item.checked == 1){
              total += parseInt(item.productNum)*parseFloat(item.salePrice);
            }
          })
          return total;
        },
        totalPrice () {
          return this.subTotal + this.shipping - this.discount + this.tax;
        }
      },
      mounted: function () {
        //获取购物车数据并过滤
        this.getCartList();
      },
      components: {
        NavHeader,
        NavBread,
        NFooter: Footer,
        Modal,
      },
      methods: {
        getCartList () {//获取购物车商品信息列表
          axios.post("/cart/list").then((res)=>{
            let resData = res.data;
            if(resData.status == 0){//获取数据失败
              console.log(resData.msg);
            }else{//获取数据成功
              if(resData.data){
                this.cartList = resData.data;
              }
            }
          }).catch((err)=>{//链接异常
            console.log(err);
          });
        },
        payment () {//生成订单
          //获取用户选择地址id
          let addressId = this.$route.query.addressId;
          axios.post('/order/add',{addressId:addressId}).then((res)=>{
            let resData = res.data;
            if(resData&&resData.status == 1){//创建订单成功
              //跳转到订单成功页面
              this.$router.push({
                path:'/orderSuccess?orderId='+resData.data,
              });
            }
          }).catch((err)=>{
            console.log(err);
          });
        }
      }
    }
</script>



