          </defs>
        </svg>
        <div class="container">
          <div class="checkout-addr">
            <div class="page-title-normal">
              <h2 class="page-title-h2"><span>check out</span></h2>
            </div>
            <!-- process step -->
            <div class="check-step">
              <ul>
                <li class="cur"><span>Confirm</span> address</li>
                <li><span>View your</span> order</li>
                <li><span>Make</span> payment</li>
                <li><span>Order</span> confirmation</li>
              </ul>
            </div>

            <!-- address list -->
            <div class="page-title-normal checkout-title">
              <h2><span>Shipping address</span></h2>
            </div>
            <div class="addr-list-wrap">
              <div class="addr-list">
                <ul>
                  <li v-for="(item,index) in addressLimitList" :class="{'check':checkedIndex == index}" @click="changeIndex(index)">
                    <dl>
                      <dt>{{item.userName}}</dt>
                      <dd class="address">{{item.streetName}}</dd>
                      <dd class="tel">{{item.tel}}</dd>
                    </dl>
                    <div class="addr-opration addr-del">
                      <a href="javascript:;" class="addr-del-btn" @click="remAddrCon(item)">
                        <svg class="icon icon-del"><use xlink:href="#icon-del"></use></svg>
                      </a>
                    </div>
                    <div class="addr-opration addr-set-default">
                      <a href="javascript:;" class="addr-set-default-btn" v-show="!item.isDefault" @click="setDefault(item)"><i>Set default</i></a>
                    </div>
                    <div class="addr-opration addr-default" v-if="item.isDefault">Default address</div>
                  </li>

                  <li class="addr-new">
                    <div class="add-new-inner">
                      <i class="icon-add">
                        <svg class="icon icon-add"><use xlink:href="#icon-add"></use></svg>
                      </i>
                      <p>Add new address</p>
                    </div>
                  </li>
                </ul>
              </div>

              <div class="shipping-addr-more" @click="expand" v-show="addressList.length>3">
                <a class="addr-more-btn up-down-btn" :class="{'open':limit>3}" href="javascript:;">
                  more
                  <i class="i-up-down">
                    <i class="i-up-down-l"></i>
                    <i class="i-up-down-r"></i>
                  </i>
                </a>
              </div>
            </div>
             shipping method
            <div class="page-title-normal checkout-title">
              <h2><span>Shipping method</span></h2>
            </div>
            <div class="lemall-msg-info hidden">
              <span>The region you selected is not within our delivery area. Please select another shipping address within our delivery areas.</span>
            </div>
            <div class="shipping-method-wrap">
              <div class="shipping-method">
                <ul>
                  <li class="check">
                    <div class="name">Standard shipping</div>
                    <div class="price">Free</div>
                    <div class="shipping-tips">
                      <p>Once shipped，Order should arrive in the destination in 1-7 business days</p>
                    </div>
                  </li>
                </ul>
              </div>
            </div>
            <div class="next-btn-wrap">
              <router-link class="btn btn--m btn--red" :class="{'btn--dis':selAddrId==-1}" :to="{path:'/orderConfirm',query:{addressId:selAddrId}}">Next</router-link>
            </div>
          </div>
        </div>
      </div>
      <modal :mdShow="mdShowFlag" v-on:close="closeModal">
          <p slot="message">
            确认删除该地址信息吗？
          </p>
          <div slot="btnGroup">
            <a href="javascript:" @click="removeAddr" class="btn btn--m">确认</a>
            <a href="javascript:" @click="mdShowFlag=false" class="btn btn--m">取消</a>
          </div>
      </modal>
      <n-footer></n-footer>
    </div>
</template>
<style>
</style>
<script>
    import '../src/assets/css/checkout.css'
    import NavHeader from './../src/components/Header.vue'
    import NavBread from '@/components/Bread.vue'
    import Footer from './../src/components/Footer.vue'
    import Modal from './../src/components/Modal.vue'
    import axios from 'axios'
    const sortBy = require('sort-array')
    const slotOrder = [ true,false ]
    export default {
      data() {
          return {
            addressList: [],//地址列表数组
            limit:3,//默认展示3条地址
            checkedIndex:0,//选中地址的索引
            remAddressId:'',//删除地址的id
            mdShowFlag:false,//默认不显示模态框,
          }
      },
      mounted: function () {
        this.getAddrList();//加载用户地址列表
      },
      computed: {
        selAddrId () {//选中的地址id
          if(this.addressList.length == 0) return -1;
          this.checkedIndex = this.checkedIndex>= this.addressList.length?this.addressList.length-1:this.checkedIndex;
          return this.addressList[this.checkedIndex].addressId;
        },
        addressLimitList () {//展示的用户地址列表
          return this.addressList.slice(0,this.limit);
        },
      },
      components: {
        NavHeader,
        NavBread,
        NFooter:Footer,
        Modal
      },
      methods: {
        getAddrList(){//获取地址列表
          axios.post('/address/list').then((res)=>{
            let resData = res ? res.data : null;
            if(resData&& resData.status == 1){//请求成功
              this.addressList = sortBy(resData.data, ['isDefault'], { isDefault: slotOrder });
            }
          }).catch((err)=>{//链接异常
            console.log(err);
          })
        },
        expand () {//展开列表
          if(this.limit == 3){//显示所有
            this.limit = this.addressList.length;
          }else {
            this.limit = 3;
          }
        },
        changeIndex (index) {//改变选中的地址
          this.checkedIndex = index;
        },
        setDefault (item) {//设置地址默认
          //获取地址的id
          let addressId = item.addressId;
          //发送请求
          axios.post('/address/default',{addressId:addressId}).then((res)=>{
            let resData = res.data;
            if(resData&&resData.status == 1){//设置成功
              //this.addressList = resData.data;
              this.addressList.forEach(function (item) {
                if(item.isDefault){
                  item.isDefault = false;
                }
              });
              item.isDefault = true;
            }
          }).catch((err)=>{
            console.log(err);
          })
        },
        remAddrCon(item) {//显示模态框问用户是否删除地址信息
          this.remAddressId = item.addressId;
          this.mdShowFlag = true;
        },
        removeAddr () {//移除地址信息
          //关闭模态框
          this.mdShowFlag = false;
          axios.post('/address/remove',{
            addressId: this.remAddressId
          }).then((res)=>{
            let resData = res.data;
            if(resData&&resData.status==1){//移除地址成功
              this.getAddrList();
            }else if(resData&&resData.status==0){
              console.log(resData.msg);
            }
          }).catch((err)=>{
            console.log(err);
          })
        },
        closeModal () {//关闭模态框
          this.mdShowFlag = false;
        }
      }
    }
</script>



